.book_info_card {
    position: relative;
    padding-top: 40px;
    padding-left: 24px;
    padding-right: 24px;
    padding-bottom: 40px;
    max-width: 335px;
    background-color: $white;
    border-radius: 18px;
    border: 2px solid $black-accent-color;
    max-height: 85vh;
    overflow-y: scroll;
    margin-top: 60px;
    @media screen and (min-width: 768px) {
      margin-top: 85px;
      max-width: none;
      width: 579px;
      padding: 40px;
      overflow-y: visible;
    }
    @media screen and (min-width: 1440px) {
      margin-top: 0;
    }
  }
  .info_img-author_container {
    @media screen and (min-width: 768px) {
      display: flex;
      gap: 24px;
      margin-bottom: 40px;
    }
  }
  .book-info-img-wrap {
    margin-bottom: 14px;
    width: 100%;
    height: auto;
    max-height: 408px;
    border: 1px solid $lt-subtitle-color;
    border-radius: 8px;
    overflow: hidden;
    @media screen and (min-width: 768px) {
      min-width: 192px;
      margin-bottom: 0;
      height: 281px;
      width: 192px;
    }
  }
  .book_info_img {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
  .book_info_name {
    margin-bottom: 4px;
    font-size: 16px;
    line-height: 1.12;
    letter-spacing: -0.04em;
    @media screen and (min-width: 768px) {
      font-size: 24px;
      line-height: 1.17;
      margin-bottom: 8px;
    }
  }
  .book_info_author {
    margin-bottom: 18px;
    font-style: italic;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: -0.04em;
    color: $lt-subtitle-color;
    @media screen and (min-width: 768px) {
      margin-bottom: 20px;
      font-size: 14px;
      line-height: 1.29;
    }
  }
  .book_info_description {
    margin-bottom: 14px;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.04em;
    @media screen and (min-width: 768px) {
      margin-bottom: 20px;
    }
  }
  .info-logo-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;
    margin-bottom: 40px;
    @media screen and (min-width: 768px) {
      margin-bottom: 0;
    }
  }
  .book-store-link {
    transition: transform $transition;
    &:hover,
    &:focus {
      transform: scale(1.15);
    }
    &:hover .store-info-book,
    &:focus .store-info-book {
      filter: grayscale(0) drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.25));
    }
  }
  .store-info-book {
    filter: grayscale(1);
    transition: filter $transition;
  }
  .btn-book-info {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 12px 18px;
    min-width: 211px;
    min-height: 46px;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.19;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    color: inherit;
    background-color: transparent;
    border: 2px solid $main-accent-color;
    border-radius: 40px;
    cursor: pointer;
    transition: color $transition, background-color $transition,
      box-shadow $transition;
    &_book_add__to_list {
      width: 215px;
    }
    &_book_remove_from_list {
      width: 279px;
    }
    &:hover {
      color: $white;
      background-color: $main-accent-color;
    }
    &:focus {
      color: $white;
      background-color: $main-accent-color;
      box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.15);
    }
    @media screen and (max-width: 350px) {
      font-size: 4vw;
    }
    @media screen and (max-width: 319.98px) {
      font-size: 12.8px;
    }
    @media screen and (min-width: 768px) {
      width: 499px;
      height: 64px;
      padding: 20px 100px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .book-infoBtn-explanation {
    margin-top: 8px;
    font-weight: 400;
    font-size: 10px;
    line-height: 1.2;
    text-align: center;
    letter-spacing: -0.04em;
    color: $lt-congrats-text-color;
    @media screen and (min-width: 768px) {
      font-size: 12px;
      line-height: 1.17;
    }
  }
  .modal-info-close {
    position: absolute;
    top: 12px;
    right: 12px;
    cursor: pointer;
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    &:hover .info-modal-close-icon,
    &:focus .info-modal-close-icon {
      stroke: $accent-text-color;
    }
    @media screen and (min-width: 768px) {
      top: 18px;
      right: 18px;
    }
  }
  .info-modal-close-icon {
    stroke: $black-accent-color;
    transition: stroke $transition;
    width: 24px;
    height: 24px;
    @media screen and (min-width: 768px) {
      width: 28px;
      height: 28px;
    }
  }